export default function (_,MESSAGE_TYPES, MESSAGE_RESTRICTIONS, Api, Message, MessageImageItem, MessageActionButton, SortableConfig) {
	"ngInject";

	var IMAGE_PROP = "img";

	var vm = this;

	if ( !_.isArray( _.get(vm,'message.items') ) ) return false;

	vm.MAX_BUTTONS_COUNT = MESSAGE_RESTRICTIONS.MAX_BUTTONS_COUNT;
	vm.MAX_IMAGES_COUNT = MESSAGE_RESTRICTIONS.MAX_IMAGES_COUNT;

	vm.id = _.uniqueId();

	vm.sortableConfig = new SortableConfig();

	vm.removeMessage = () => {
		vm.message.remove();
	};
	vm.addButton = (currentImage) => {
		vm.message.addButtonToImage(currentImage).then((newButton) => {
			newButton._isEdit = true;
		})
	};

	vm.addImage = () => {
		vm.message.addImage(); // returns image
	};
	vm.removeImage = (image) => {
		image.remove();

		if (_.get(vm,'message.items.length') < 1) {
			vm.removeMessage();
		}
	};

	vm.files = [];
	vm.onFileChange = (e,files,target) => {
		console.warn('gallery filechange',e,target);
		Api.upload(files[0])
			.then((response={})=> {
				let imagePath = response.path
				if (imagePath) target[IMAGE_PROP] = imagePath;
			})
			.catch((err)=> {
				console.warn("upload error",err)
			})
	};

	if (!_.get(vm,'message.items.length')) vm.addImage();

	return vm;
}



