export default function ($rootScope, $http, Api) {
	"ngInject";

	const stateEvents = {
		'$stateChangeStart': (event, toState, toParams, fromState, fromParams) => {
			console.info('$stateChangeStart', event, toState, toParams, fromState, fromParams);
		},
		'$stateChangeSuccess': (event, toState, toParams, fromState, fromParams) => {
			console.info('$stateChangeSuccess', event, toState, toParams, fromState, fromParams);
		},
		'$stateNotFound': (event, unfoundState, fromState, fromParams) => {
			console.warn('$stateNotFound', event, unfoundState, fromState, fromParams);
		},
		'$stateChangeError': (event, toState, toParams, fromState, fromParams, error) => {
			console.error('$stateChangeError', event, toState, toParams, fromState, fromParams, error);
		}
	};

	for (let event in stateEvents) {
		$rootScope.$on(event, stateEvents[event]);
	}


	window.appDebug = {
		$rootScope,
		$http,
		Api
	}
}



