let _ = window._;

export default class Ctrl {

	constructor(_, $q, $scope, $state, ENTITY_TYPES, BroadcastQueuesService, MessageActionButtonService) {
		"ngInject";

		this.$q = $q;
		// this.model - from bindings
		this.collection = this.model;
		this.ENTITY_TYPES = ENTITY_TYPES;
		this.path = [];
		this.$scope = $scope;
		this.$state = $state;
		this.BroadcastQueuesService = BroadcastQueuesService;
		this.MessageActionButtonService = MessageActionButtonService;
	}

	$onInit() {
		console.log('ready');

		this.setPath();
		this.$scope.$on('$stateChangeSuccess', this.setPath.bind(this));
	}

	setPath() {
		this.getCurrentPath(this.collection, _.get(this, '$state.params.id'))
			.then((path) => {
				this.path = path;
			})
	}

	isActiveState(point) {
		if (this.collection.length < 2 && !this.$state.params.id) return true;
		let isActiveState = this.$state.includes('broadcast.child', {
			id: _.get(point, 'self._meta.id')
		});
		return isActiveState;
	}

	getCurrentPath(rootCollection, currentId) {

		let defer = this.$q.defer();
		let result = [];
		let collectMethod = "unshift";

		if (!currentId || rootCollection.length < 2) {
			this.BroadcastQueuesService.getRoot().then((queue)=>{

				defer.resolve( [{self: queue}] );

			});

			return defer.promise;
		}

		this.getPoint("current",currentId).then((startPoint)=> {
			result[collectMethod](startPoint);

			let pointId = _.get(startPoint, 'self._meta.parentId');
			this.promiseWhile(() => {
				return !!pointId;
			}, () => {
				return this.getPoint("parent",pointId)
					.then((point) => {
						result[collectMethod](point);
						pointId = _.get(point, 'self._meta.parentId');
					})
			}).then(() => {

				defer.resolve(result);

			}).catch((err)=> {
				console.warn("async while catch", err);
			});
		});

		return defer.promise;
	}

	/**
	 *
	 * @param type {String} [ "current" || "parent" ] get current point or parent point ("current" at first time)
	 * @param pointId {String|Number} id of point of search
	 * @returns {Object} format: {self - entity of search, parent - exact parent, whitch has "id" equals to self "parentId"}
	 */
	getPoint(type="current",pointId) {

		if (!pointId) return this.$q.reject('no pointId');

		let defer, method, result={
			self:null,
			parent:null
		};

		defer = this.$q.defer();

		method = (()=>{
			switch (type) {
				case "current":
					return "getById";
				case "parent":
					return "getByInnerButtonId";
			}
		})();

		this.BroadcastQueuesService[method](pointId)
			.then((queue) => {

				result.self = queue;

				let parentButtonId = _.get(queue, '_meta.parentId');
				if (parentButtonId) {
					return this.MessageActionButtonService.getById(parentButtonId);
				} else {
					return this.$q.resolve(null);
				}
			}).then((actionButton) => {
				result.parent = actionButton;

				defer.resolve(result);
			});

		return defer.promise;
	}

	/**
	 *
	 * @param condition {Function} function that returns a boolean
	 * @param body {Function} function that returns a promise
	 * @returns {Promise} promise for the completion of the loop
	 */
	promiseWhile(condition, body) {
		var done = this.$q.defer();

		let loop = () => {
			if (!condition()) return done.resolve();
			this.$q.when(body(), loop, done.reject);
		};

		setTimeout(loop);

		// The promise
		return done.promise;
	}

}



