const __DEBUG__ = false;
import "./app.less";
import onRun from "./app.run"
import appConfig from './app.config';
import BroadcastBuilder from './BroadcastBuilder/broadcastBuilder.module.js';
import Helpers from './helpers';

angular
	.module('app', [
		"vendor",
		BroadcastBuilder.name
	])

	.constant('__DEBUG__', __DEBUG__)
	.config(appConfig)

	.controller('RootCtrl', function () {
		var root = this;
		return root;
	})

	.service("Helpers",Helpers)

	.directive("fileread", function () {
		"ngInject";

		var returnFalse = function (scope) {
			scope.$apply(function () {
				scope.fileread = "";
			});
		};

		return {
			scope: {
				fileread: "="
			},
			link: function (scope, element) {
				element.bind("change", function (e) {
					if (!e || !e.target || !e.target.files || !e.target.files[0]) {
						return returnFalse(scope);
					}

					let file = e.target.files[0];

					var reader = new FileReader();
					reader.onload = function (loadEvent) {
						scope.$apply(function () {
							scope.fileread = loadEvent.target.result;
						});
					};
					reader.onerror = function (error) {
						console.warn('FileReader error:', error);
					};
					reader.readAsDataURL(file);

				});
			}
		}
	})

	.directive("fileChange", function ($parse) {
		"ngInject";

		return {
			// restrict: 'A',
			// link: function (scope, element, attrs) {
			// 	var onChangeHandler = scope.$eval(attrs.fileChange);
			// 	element.bind('change', (...args) => {
			// 		onChangeHandler.apply(scope,args);
			// 	});
			// }

			require: 'ngModel',
			restrict: 'A',
			link: function ($scope, element, attrs, ngModel) {

				ngModel;
				// Get the function provided in the file-change attribute.
				// Note the attribute has become an angular expression,
				// which is what we are parsing. The provided handler is
				// wrapped up in an outer function (attrHandler) - we'll
				// call the provided event handler inside the handler()
				// function below.
				var attrHandler = $parse(attrs['fileChange']);

				// This is a wrapper handler which will be attached to the
				// HTML change event.
				var handler = function (e) {

					$scope.$apply(function () {

						// Execute the provided handler in the directive's scope.
						// The files variable will be available for consumption
						// by the event handler.
						attrHandler($scope, { $event: e, files: e.target.files, $scope: $scope });
					});
				};

				// Attach the handler to the HTML change event
				element[0].addEventListener('change', handler, false);
			}

		};
	})

	.filter('unsafe', function ($sce) {
		"ngInject";

		return function (val) {
			return $sce.trustAsHtml(val);
		};
	})

	.directive('contenteditable', function ($sce) {
		"ngInject";

		return {
			require: "?ngModel",
			restrict: "A",

			link: function (scope, $el, attrs, ngModel) {
				if (!ngModel) return; // do nothing if no ng-model

				// Specify how UI should be updated
				ngModel.$render = function () {
					$el.html($sce.getTrustedHtml(ngModel.$viewValue || ''));
				};

				// Listen for change events to enable binding
				$el.on('blur keyup change', function () {
					scope.$evalAsync(read);
				});


				// Write data to the model
				function read() {
					var html = $el.html();
					html = html
						.replace(/<div>/gi, ' ')
						.replace(/[<\/div>|<br>]/gi, '');
					// When we clear the content editable the browser leaves a <br> behind
					// If strip-br attribute is provided then we strip this out
					if (attrs.stripBr && html === '<br>') {
						html = '';
					}
					ngModel.$setViewValue(html);
				}

			}
		}
	})

	.directive('elastic', function ($timeout) {
		"ngInject";
		return {
			restrict: 'A',
			link: function ($scope, element) {
				$scope.initialHeight = $scope.initialHeight || element[0].style.height;
				element[0].style.overflow = "hidden";
				var resize = () => {
					element[0].style.height = $scope.initialHeight;
					element[0].style.height = "" + (element[0].scrollHeight) + "px";
				};
				element.on("input paste change", resize);
				$timeout(resize, 0);
			}
		};
	})

	.directive('maxLength', function ($compile) {
		"ngInject";

		return {
			restrict: "A",
			require: "?ngModel",
			link: function (scope, $el, attrs) {
				let maxLength = attrs.maxLength;

				let ngExpression = `${maxLength}-${attrs.ngModel}.length`;

				scope.getColor = (val) => {
					if (val > 0) {
						return "#999";
					} else {
						return "#E9595B";
					}
				};

				var content = `<div max-length-value ng-style="{color: getColor(${ngExpression}) }">{{ ${ngExpression} }}</div>`;
				var compiled = $compile(content);
				$el.after(compiled(scope));
			}
		}
	})

	.directive('willFocus', function ($timeout) {
		"ngInject";

		return {
			restrict: "A",
			link: function ($scope, $el, attrs) {
				let willFocusThrow = attrs.willFocus || 200;
				let el = $el[0];
				$timeout(()=> {
					try {
						el.focus();
					} catch (err) {
						console.warn('willFocus fails', err);
					}
				}, willFocusThrow)
			}
		}
	})


	.run(onRun)
;



