export default function (_,MESSAGE_GALLERY_ITEM_SCHEME, ENTITY_TYPES) {
	"ngInject";

	class ImageItem {
		constructor(data={}) {

			let defaults = {
				_meta: {
					id: _.uniqueId(),
					parentId: undefined,
					type: ENTITY_TYPES.GALLERY_ITEM
				}
			};

			angular.extend( this,
				angular.copy(MESSAGE_GALLERY_ITEM_SCHEME),
				defaults,
				data
			)
		}
	}

	return ImageItem;
}



