export default function (_, Api) {
	"ngInject";

	var vm = this;
	var message = vm.message;

	var IMAGE_PROP = "img";

	vm.id = _.uniqueId();
	vm.removeMessage = () => {
		message.remove();
	};

	vm.onFileChange = (e,files,target) => {
		console.warn('image filechange',e,target);
		Api.upload(files[0])
			.then((response={})=> {
				let imagePath = response.path
				if (imagePath) target[IMAGE_PROP] = imagePath;
			})
			.catch((err)=> {
				console.warn("upload error",err)
			})
	};

	return vm;
}



