export default function (_,$q, MessageActionButton, Helpers) {
	"ngInject";

	var buttons = [];

	return {
		getAll: () => {
			let p = $q.defer();
			p.resolve(buttons);
			return p.promise;
		},
		getById: (id) => {
			id +="";
			let button = _.find(buttons, {_meta: {id} } );

			let p = $q.defer();
			p.resolve(button);
			return p.promise;
		},
		add: (data) => {
			let p = $q.defer();

			var button = new MessageActionButton(data);
			buttons.push(button);

			p.resolve(button);
			return p.promise;

		},
		remove: (queue) => {
			Helpers.remove(buttons, queue);
		}
	}
}



