export default function (_,$timeout, MESSAGE_TYPES, MESSAGE_RESTRICTIONS) {
	"ngInject";

	this.MAX_BUTTONS_COUNT = MESSAGE_RESTRICTIONS.MAX_BUTTONS_COUNT;

	let message = this.message;

	this.id = _.uniqueId();
	this.removeMessage = () => {
		message.remove();
	};

	this.addButton = () => {
		message.addButton().then((newButton) => {
			newButton._isEdit = true;
		})
	};
}



