export default function (_,$timeout, $state, MESSAGE_ACTION_TYPES,SCROLL_EVENT_NAME,$scope, Helpers) {
	"ngInject";

	// TODO [scroll] refactor this;
	$scope.$on(SCROLL_EVENT_NAME,()=>{
		$scope.$applyAsync(()=>{
			this.isEdit = false;
		});
	})
	//[scroll]

	this.id = _.uniqueId();
	this.MESSAGE_ACTION_TYPES = MESSAGE_ACTION_TYPES;
	this.removeButton = (data) => {
		Helpers.remove(this.parentArray, data);
	};
	this.saveButton = () => {
		this.isEdit = false;
	};

	this.editMessages = (button) => {
		console.warn('editQueue', button)
		if (button._meta.queueId) {

			$state.go('broadcast.child', {
				id: button._meta.queueId,
			}, {
				location: false
			})

		} else {

			this.button.addMessages().then((messagesQueue) => {
				$state.go('broadcast.child', {
					id: messagesQueue._meta.id,
				}, {
					location: false
				})
			});

		}

	};


	if (this.button._isEdit) { // TODO find better solution
		$timeout(()=> {
			this.isEdit = true;
			delete this.button._isEdit
		})
	}
}



