export default function (_,$q, BroadcastQueue, Helpers) {
	"ngInject";

	var queues = [];

	return {
		getAll: () => {
			return $q.resolve( queues );
		},
		getRoot: () => {
			return $q.resolve( _.find(queues,{
				isRoot:true
			}))
		},
		getById: (id) => {
			id +="";
			return $q.resolve( _.find(queues, {
				_meta: {id}
			}));
		},
		getByInnerButtonId: (parentId) => {

			let resolve = (arr) => {
				return _.find(arr, (el) => {
					return _.get(el, '_meta.id') === parentId;
				})
			};

			return $q.resolve( _.find(queues, (el = {}) => {
				return _.find(el.messages, (el = {}) => {
					if (_.get(el, 'items.length')) {

						return _.find(el.items, (el = {}) => {
							return resolve(el.buttons);
						})

					} else if (el.buttons) {

						return resolve(el.buttons)

					}
				})
			}));
		},
		addQueue: (type,data) => {
			var queue = new BroadcastQueue(type,data);
			queues.push(queue);

			return $q.resolve(queue);

		},
		removeQueue: (queue) => {
			Helpers.remove(queues, queue);
		}
	}
}



