export default function ($stateProvider) {
	"ngInject";

	$stateProvider.state("broadcast", {
		abstract: true,
		resolve: {
			// @ngInject
			queues (BroadcastQueuesService) {
				return BroadcastQueuesService.getAll();
			},
		},
		// @ngInject
		onEnter: ($stateParams) => {
			console.warn(`state broadcast`, $stateParams);

			return {};
		},
		template: `
			<broadcast-builder-breadcrumbs model="$resolve.queues"></broadcast-builder-breadcrumbs>
			<ui-view></ui-view>
		`
	});
	$stateProvider.state("broadcast.child", {
		url: '/*id', // url is not in use, fake id
		params: {
			id: {
				value: undefined,
				squash: true
			}
		},
		resolve: {
			// @ngInject
			messagesQueue: ($stateParams, $state, $q, QUEUE_TYPES, BroadcastQueuesService) => {
				let defer = $q.defer();
				let id = $stateParams.id;

				let initialQueue = {};

				if (window.BROADCAST_MESSAGES) {
					console.warn('BROADCAST_MESSAGES !!',window.BROADCAST_MESSAGES);
					initialQueue.messages = angular.copy(window.BROADCAST_MESSAGES);
					delete window.BROADCAST_MESSAGES;
				}

				if (id) {

					BroadcastQueuesService.getById(id).then((found) => {
						defer.resolve( found
							? found
							: BroadcastQueuesService.addQueue( QUEUE_TYPES.POST, initialQueue )
						)
					});

				} else {

					defer.resolve(BroadcastQueuesService.addQueue( QUEUE_TYPES.POST, initialQueue ) );

				}

				return defer.promise;
			}
		},
		// @ngInject
		onEnter: ($stateParams) => {
			console.warn(`state broadcast.child`, $stateParams);
			return {};
		},
		template: '<broadcast-builder queue="$resolve.messagesQueue"></broadcast-builder>',
		controllerAs: "vm"
	})
	;

}



