export default function (_,MESSAGE_QUEUE_SCHEMES,ENTITY_TYPES,QUEUE_TYPES, Message, Helpers) {
	"ngInject";

	var isRoot = true;

	class BroadcastQueue {
		constructor(type,data={}) {
			if (typeof type === "undefined") {
				type = QUEUE_TYPES.POST
			}

			let defaults = {
				title: "Queue",
				_meta: {
					id: _.uniqueId(),
					parentId: undefined,
					type: ENTITY_TYPES.QUEUE
				}
			};

			angular.extend(this,
				angular.copy(MESSAGE_QUEUE_SCHEMES[type]),
				defaults,
				data
			);

			if (isRoot) {
				this.isRoot = true;
				isRoot = false
			}

			this.initialize();
		}

		initialize () {

			if (_.get(this,'messages.length')) {
				let messages = angular.copy(this.messages);
				this.messages.length = 0;
				_.each(messages,(message) => {
					this.addMessage(message.type,message)
				})
			}

		}

		addMessage(type,data) {
			var message = new Message(type,data);
			message._meta.parentId = this._meta.id;
			message.remove = () => {
				Helpers.remove(this.messages, message);
			};
			this.messages.push(message);
			return message;
		}

		removeMessage(message) {
			return Helpers.remove(this.messages, message);
		}

	}

	return BroadcastQueue;
}



