export default function (_,$q,ENTITY_TYPES,MESSAGE_TYPES, MESSAGE_SCHEMES, MESSAGE_RESTRICTIONS,MessageActionButtonService, MessageActionButton, MessageImageItem, Helpers) {
	"ngInject";

	class Message {
		constructor(type,data={}) {
			type || (type = MESSAGE_TYPES.TEXT);
			if (typeof type === "undefined" || !(type in MESSAGE_SCHEMES)) {
				type = MESSAGE_TYPES.TEXT;
			}

			let defaults = {
				_meta: {
					id: _.uniqueId(),
					parentId: undefined,
					type: ENTITY_TYPES.MESSAGE
				}
			};

			angular.extend( this,
				angular.copy(MESSAGE_SCHEMES[type]),
				defaults,
				data
			);

			this.initialize();
		}

		initialize () {
			// if this buttons.length
			// if this items.length

			if (_.get(this,'buttons.length') ) {
				let buttons = angular.copy(this.buttons);
				this.buttons.length = 0;

				// TODO wait for all buttons
				_.each(buttons,(buttonData)=> {
					this.addButton(buttonData);
				})
			}
			if (_.get(this,'items.length') ) {
				let items = angular.copy(this.items);
				this.items.length = 0;
				_.each(items,(itemData)=> {
					let newImage = this.addImage(itemData);

					if (_.get(newImage,'buttons.length')) {
						let buttons = angular.copy(newImage.buttons);
						newImage.buttons.length = 0;

						// TODO wait for all buttons
						_.each(buttons,(buttonData)=> {
							this.addButtonToImage(newImage,buttonData)
						})
					}
				})
			}
		}

		addButton (data) {
			if ( !_.isArray(_.get(this,'buttons')) ) return $q.reject(null);
			if (this.buttons.length >= MESSAGE_RESTRICTIONS.MAX_BUTTONS_COUNT) return $q.reject(null);

			return MessageActionButtonService.add(data).then((newButton)=>{
				newButton._meta.parentId = this._meta.id;
				newButton.remove = () => {
					Helpers.remove(this.buttons, newButton)
				};
				this.buttons.push(newButton);

				return newButton;
			});
		}

		addImage (data) {
			if (this.items.length >= MESSAGE_RESTRICTIONS.MAX_IMAGES_COUNT) return false;

			var newImage = new MessageImageItem(data);
			newImage._meta.parentId = this._meta.id;
			newImage.remove = () => {
				Helpers.remove(this.items, newImage);
			};

			this.items.push(newImage);
			return newImage;
		}

		addButtonToImage (currentImage, data) {

			if ( !_.isArray(_.get(currentImage,'buttons')) ) return $q.reject(null);

			if (currentImage.buttons.length >= MESSAGE_RESTRICTIONS.MAX_BUTTONS_COUNT) return $q.reject(null);

			return MessageActionButtonService.add(data).then((newButton)=>{
				newButton._meta.parentId = this._meta.id;
				newButton.remove = () => {
					Helpers.remove(currentImage.buttons, newButton)
				};
				currentImage.buttons.push(newButton);
				return newButton;
			});
		}

	}

	return Message;
}



