export default function (_,MESSAGE_ACTION_TYPES,SCROLL_EVENT_NAME,$scope) {
	"ngInject";

	// TODO [scroll] refactor this;
	$scope.$on(SCROLL_EVENT_NAME,()=>{
		$scope.$applyAsync(()=>{
			this.isOpen = false;
		})
	});
	//[scroll]

	this.id = _.uniqueId();

	this.MESSAGE_ACTION_TYPES = MESSAGE_ACTION_TYPES;

	this.select = (selectedValue) => {
		this.method(selectedValue);
	};
}



