import {
	ENTITY_TYPES,
	QUEUE_TYPES,
	MESSAGE_TYPES,
	MESSAGE_ACTION_TYPES,
	MESSAGE_QUEUE_SCHEMES,
	MESSAGE_SCHEMES,
	MESSAGE_BUTTON_SCHEME,
	MESSAGE_GALLERY_ITEM_SCHEME,
	MESSAGE_RESTRICTIONS
} from "./broadcastBuilder.constants";

import "./common/templates/valueSelect.html";
import "./common/templates/actionButtonEdit.html";

import Api from "./broadcastBuilder.api"
import BreadcrumbsCom from "./breadcrumbs/breadcrumbs.com";
import MessageQueuesService from "./common/messageQueues.service";
import MessageQueueFactory from "./common/MessageQueue.factory";
import MessageFactory from "./common/Message.factory";
import MessageButtonService from "./common/MessageButton.service";
import MessageButtonFactory from "./common/MessageButton.factory";
import MessageImageItem from "./common/MessageGalleryItem.factory";
import AddMessageButtonCom from "./addButton/addButton.com";
import MessageButtonCom from "./messageButton/messageButton.com";
import BroadcastBuilderCom from "./broadcastBuilder.com";

import MessageTextCom from "./messages/text/messageText.com";
import MessageImageCom from "./messages/image/messageImage.com";
import MessageGalleryCom from "./messages/gallery/messageGallery.com";
import initialize from "./broadcastBuilder.run"

import router from "./broadcastBuilder.router"

import SortableConfig from "./common/SortableConfig.factory";

export default angular.module('broadcastBuilder', [])

	.constant('ENTITY_TYPES', ENTITY_TYPES)
	.constant('QUEUE_TYPES', QUEUE_TYPES)
	.constant('MESSAGE_TYPES', MESSAGE_TYPES)
	.constant('MESSAGE_ACTION_TYPES', MESSAGE_ACTION_TYPES)
	.constant('MESSAGE_RESTRICTIONS', MESSAGE_RESTRICTIONS)

	.constant('MESSAGE_QUEUE_SCHEMES', MESSAGE_QUEUE_SCHEMES)
	.constant('MESSAGE_SCHEMES', MESSAGE_SCHEMES)
	.constant('MESSAGE_BUTTON_SCHEME', MESSAGE_BUTTON_SCHEME)
	.constant('MESSAGE_GALLERY_ITEM_SCHEME', MESSAGE_GALLERY_ITEM_SCHEME)

	.constant("SCROLL_EVENT_NAME", "builder-scroll")

	.constant("API_URL", {
		BASE: window.API_URL || "http://outloud.pro",
		UPLOAD: window.UPLOAD_URL || "http://outloud.pro/upload"
	})

	.config(router)

	.service("Api",Api)

	.service('BroadcastQueuesService', MessageQueuesService)

	.factory('BroadcastQueue', MessageQueueFactory)

	.factory('Message', MessageFactory)

	.service('MessageActionButtonService', MessageButtonService)

	.factory('MessageActionButton', MessageButtonFactory)

	.factory('MessageImageItem', MessageImageItem)

	.factory('SortableConfig', SortableConfig)

	.component('broadcastBuilder', BroadcastBuilderCom)

	.component("broadcastBuilderBreadcrumbs",BreadcrumbsCom)

	.component('messageText', MessageTextCom)

	.component('messageImage', MessageImageCom)

	.component('messageGallery', MessageGalleryCom)

	.component('messageButton', MessageButtonCom)

	.component('addButton',AddMessageButtonCom)

	.run(initialize)
;



