export default function (_,ENTITY_TYPES,MESSAGE_ACTION_TYPES, MESSAGE_BUTTON_SCHEME, $injector) {
	"ngInject";

	var BroadcastQueuesService = null;

	class MessageActionButton {
		constructor(data={}) {

			let defaults = {
				title: "New Button",
				type: MESSAGE_ACTION_TYPES.SEND_MESSAGE,
				_meta: {
					id: _.uniqueId(),
					parentId: undefined,
					type: ENTITY_TYPES.BUTTON
				}
			};

			angular.extend( this,
				angular.copy(MESSAGE_BUTTON_SCHEME),
				defaults,
				data
			);

			this.initialize();
		}

		initialize () {
			if (_.get(this,'messages.length')) {
				let messages = angular.copy(this.messages);
				this.messages.length = 0;
				this.addMessages({messages})
			}
		}

		addMessages (data) {

			// TODO this is circular dependency hack, FIX IT
			if (!BroadcastQueuesService) BroadcastQueuesService = $injector.get('BroadcastQueuesService');

			return BroadcastQueuesService.addQueue(undefined, data).then((messagesQueue) => {
				// main linking
				messagesQueue._meta.parentId = this._meta.id;
				this._meta.queueId = messagesQueue._meta.id;
				this.messages = messagesQueue.messages;

				return messagesQueue;
			});
		}

	}

	return MessageActionButton;
}



