export default function (_,$timeout,__DEBUG__,MESSAGE_TYPES,SCROLL_EVENT_NAME, SortableConfig, BroadcastQueuesService,$element,$scope) {
	"ngInject";

	var vm = this;
	vm.__DEBUG__ = __DEBUG__;

	vm.id = _.uniqueId();

	vm.MESSAGE_TYPES = MESSAGE_TYPES;

	vm.messagesQueue || (console.warn('broadcastBuilder.ctrl needs an messagesQueue')); //from bindings

	vm.messages = vm.messagesQueue.messages;

	vm.messageTypesForSelect = [
		{value: MESSAGE_TYPES.TEXT, title: "Add text card"},
		{value: MESSAGE_TYPES.IMAGE, title: "Add image card"},
		{value: MESSAGE_TYPES.GALLERY, title: "Add gallery"}
	];

	vm.sortableConfig = new SortableConfig();

	vm.addMessage = (...args) => {
		if (!vm.messagesQueue) return false;
		vm.messagesQueue.addMessage(...args);
	};

	vm.onMessageTypeSelect = (msgType = {}) => {
		vm.addMessage(msgType.value);
	};

	vm.startBroadcast = () => {
		vm.serializeData().then((data)=> {
			console.warn('MESSAGES:', data);
		})
	};

	vm.parseData =  () => {

	};

	vm.serializeData = () => {

		function serializeButtons (buttons=[]) {
			_.each(buttons,(button) => {
				delete button._meta;
				serializeMessages(button.messages);
			});
			return buttons;
		}

		function serializeMessages (messages) {
			return _.chain(messages)
				.each((message) => {
					delete message._meta;

					serializeButtons(_.get(message,'buttons',[]));
					_.each(_.get(message,'items',[]), (item) => {
						delete item._meta;
						serializeButtons(_.get(item,'buttons',[]))
					})
				})
				.value();
		}

		return BroadcastQueuesService.getRoot().then((queue={})=>{
			queue = angular.copy(queue);
			return angular.toJson( serializeMessages(queue.messages||[]) , true)
		});

		// return BroadcastQueuesService.getRoot().then((queue={})=>{
		// 	return angular.toJson( queue.messages , true)
		// });
	};

	// TODO [scroll] refactor this;
	let $scrollable = angular.element($element[0].querySelector('.builder-inner'));
	$scrollable.on('scroll', (...args) => {
		$scope.$broadcast(SCROLL_EVENT_NAME,...args);
	});
	//[scroll]

	if (__DEBUG__) {
		// $timeout(()=> {
		// 	vm.addMessage(MESSAGE_TYPES.TEXT);
		// 	vm.addMessage(MESSAGE_TYPES.IMAGE);
		// 	vm.addMessage(MESSAGE_TYPES.GALLERY);
		// },300);

		BroadcastQueuesService.getAll().then((queues)=> {
			vm.queues = queues;
		});
	}

	return vm;
}



